    methods: {
        handleClick(tab, event) {
            // console.log(tab, event);
        },
        getValue(value){
            console.log(value)
            var mychart = this.$echarts.init(document.getElementById(value[0]))
            if(value.length>4){
                this.stack.xAxis[0].axisLabel.rotate = value[1]
                this.stack.xAxis[0].data = value[2]
                this.stack.legend.data = value[3]
                this.stack.series = value[4]
                mychart.clear();
                mychart.setOption(this.stack,true)
            }else{
                this.baroption.xAxis[0].axisLabel.rotate = value[1]
                this.baroption.xAxis[0].data = value[2]
                this.baroption.series[0].data = value[3]
                mychart.clear();
                mychart.setOption(this.baroption,true)
            }
        }
    },
    components: {
        barCharts,
    },
    mounted() {
        var _this = this
        $.ajax({
            type: "post",
            url: _this.baseUrl+"/ydjxzg/hx/xsgl.action",
            data: {reg:'zyxsqk',style:'1'},
            dataType: "json",
            success: function (res) {
                console.log(res)
                if(res.zjxsslpm){
                    _this.xData = []
                    _this.yData = []
                   // _this.baroption1.param.push(res.zjxsslpm[0].param1)
                    for(var i = 1;i < res.zjxsslpm.length;i++){
                        _this.xData.push(res.zjxsslpm[i].mc)
                        _this.yData.push(res.zjxsslpm[i].zcount)
                    }
                    _this.baroption1.yAxis[0].name = '(人)'
                    _this.baroption1.xAxis[0].data = _this.xData;
                    _this.baroption1.series[0].data = _this.yData;
                }
                if(res.xslspm){
                    _this.xData = []
                    _this.yData = []
                    //_this.baroption2.param.push(res.xslspm[0].param1)
                    for(var i = 1;i < res.xslspm.length;i++){
                        _this.xData.push(res.xslspm[i].mc)
                        _this.yData.push(res.xslspm[i].zcount)
                    }
