                    _this.baroption3.xAxis[0].data = _this.xData;
                    _this.baroption3.series[0].data = _this.yData;
                }
                if(res.gbggkpjcj){
                    _this.xData = []
                    _this.yData = []
                   // _this.baroption3.param.push(res.zyjnpm[0].param1)
                    for(var i = 0;i < res.gbggkpjcj[1].length;i++){
                        _this.xData.push(res.gbggkpjcj[1][i].mc)
                        _this.yData.push(res.gbggkpjcj[1][i].zcount)
                    }
                    _this.baroption4.yAxis[0].name = '(人)'
                    _this.baroption4.xAxis[0].data = _this.xData;
                    _this.baroption4.series[0].data = _this.yData;
