<style>
#xsqk{
    width: 100%;
    height: 100%;
}
#xsqk .row{
    display:flex;
    justify-content:center;
}
#xsqk .row .col{
    width:750px;
    margin-top:20px;
}
#xsqk .row .cardtitle{
    font-size:25px;
    text-align:center;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
}
#xsqk .row .coltips{
    min-width:280px;
    max-width:300px;
    margin-top:20px;
}
#xsqk .row .coltips2{
    min-width:150px;
    max-width:300px;
    margin-top:20px;
}
#xsqk .shaixuan_box{
    position:relative;
    left:13%;
    bottom:32px;
}
@media all and (max-width: 1200px) {
    #xsqk .shaixuan_box{
        left: 3%!important;
    }
}
#xsqk .row .tipheight{
    height:435px;
}
#xsqk .row .tip_select{
    height:465px;
}
#xsqk .row .tips{
    font-size:25px;
    text-align:left;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
    padding:0 20px;
}
#xsqk .row .tippoint{
    margin-top:50px;
    padding:0 20px;
    line-height:40px;
}
#xsqk .is-active{
    color: #029348 !important;
}
.el-tabs__active-bar{
    background: #029348 !important;
}
#xsqk .is-top :hover{
    color: #029348 !important;
}
#xsqk .is-left :hover{
    color: #029348 !important;
}
#xsqk .el-card{
    padding: 40px 0 40px 0;
}
#xsqk .shaixuan{
    padding: 82px 0 32px 0;
}
.el-tabs__content{
    overflow:inherit;
    padding-bottom: 20px;
}
.el-card{
    transition: 0s;
}
</style>



